 los días sábados y domingos, incluso los días feriados. Por lo general, se hace una pausa de una hora para el almuerzo, sin embargo, esto también depende de la ciudad donde se encuentre. ")])]),_c('div',{staticClass:"col-lg-auto order-1 order-lg-2 mb-4 mb-lg-0"},[_c('figure',[_c('img',{staticClass:"m-auto",staticStyle:{"width":"250px"},attrs:{"src":require("@/assets/curso/tema2/t2_7_04.svg"),"alt":"Cupones"}})])])]),_c('div',{staticClass:"row",attrs:{"titulo":"Citas y lugares de encuentro  "}},[_c('div',{staticClass:"col-lg-8 order-2 order-lg-1"},[_c('p',[_vm._v("Las citas de negocio deben ser programadas, por lo menos, con una semana de antelación. Es usual hacer el primer contacto por teléfono o bien por correo electrónico. Es recomendable que las citas sean programadas con los empresarios de mayor nivel. Igualmente, se deben confirmar las citas y llegar unos días antes de la fecha agendada, esto le permitirá adaptarse al clima y a la altura y ultimar detalles finales para su reunión de negocios. La puntualidad no es un fuerte en los colombianos, como en la mayoría de los países latinoamericanos, es posible que tenga que esperar a sus socios de 15 a 20 minutos. Las primeras citas tendrán lugar en las oficinas o instalaciones de la empresa. Ocasionalmente se pueden reunir en un restaurante o en el hotel donde se aloje. ")])]),_c('div',{staticClass:"col-lg-auto order-1 order-lg-2 mb-4 mb-lg-0"},[_c('figure',[_c('img',{staticClass:"m-auto",staticStyle:{"width":"250px"},attrs:{"src":require("@/assets/curso/tema2/t2_7_05.svg"),"alt":"Cupones"}})])])]),_c('div',{staticClass:"row",attrs:{"titulo":"Vestuario"}},[_c('div',{staticClass:"col-lg-8 order-2 order-lg-1"},[_c('p',[_vm._v("Colombia es un país con una diversidad de climas, así como tiene ciudades frías, también las hay con climas bastante calurosos. Esto definirá la forma en la que deba ir presentado a una reunión de negocios, claro está que siempre será mejor ir lo más formal posible. Si se va a reunir en ciudades con climas fríos o templados, puede usar un traje, camisa, corbata y zapatos formales en el caso de los hombres. Para las mujeres se recomienda un sastre, faldas no muy cortas, tacones y usar poco maquillaje. En el caso de reunirse en una de las ciudades de clima caliente de Colombia, pude usar algo más liviano como un pantalón de dril, camisa y zapatos. Para las mujeres un vestido o un pantalón de tela fresca, blusa y tacones no muy altos. Puede optar por el uso de jean, camisa y botas, si la reunión es en una fábrica, un puerto o una finca. ")])]),_c('div',{staticClass:"col-lg-auto order-1 order-lg-2 mb-4 mb-lg-0"},[_c('figure',[_c('img',{staticClass:"m-auto",staticStyle:{"width":"250px"},attrs:{"src":require("@/assets/curso/tema2/t2_7_06.svg"),"alt":"Cupones"}})])])]),_c('div',{staticClass:"row",attrs:{"titulo":"Obsequios"}},[_c('div',{staticClass:"col-lg-8 order-2 order-lg-1"},[_c('p',[_vm._v("Los colombianos son muy amables, cordiales y descomplicados, por lo que los obsequios serán bien recibidos. Algo representativo de su país o de su empresa será bien visto. Procure que sus obsequios no sean tan ostentosos y en lo posible tratar de llevar un obsequio a todos los asistentes de la reunión, no importa el valor, es más apreciado el detalle. Si va a regalar flores evite los claveles y los gladiolos, ya que están asociados con la muerte. ")])]),_c('div',{staticClass:"col-lg-auto order-1 order-lg-2 mb-4 mb-lg-0"},[_c('figure',[_c('img',{staticClass:"m-auto",staticStyle:{"width":"250px"},attrs:{"src":require("@/assets/curso/tema2/t2_7_07.svg"),"alt":"Cupones"}})])])]),_c('div',{staticClass:"row",attrs:{"titulo":"Saludo"}},[_c('div',{staticClass:"col-lg-8"},[_c('p',[_vm._v("En los ambientes de negocios colombianos se saluda con un apretón de manos, tanto a hombres como a mujeres, debe extenderla primero a la persona de mayor rango en la empresa. No salude a su contraparte femenina con beso en la mejilla, a menos que tenga un grado de confianza que le permita hacerl